class SingleItem {
  constructor(cookies){
    this.cookies = cookies
  }

  setCookie(cookieKey, cookieValue, expirationDays){
    let expiryDate = '';

    if (expirationDays) {
      const date = new Date();

      date.setTime(`${date.getTime()}${(expirationDays || 30 * 24 * 60 * 60 * 1000)}`);

      expiryDate = `; expiryDate=" ${date.toUTCString()}`;
    }

    document.cookie = `${cookieKey}=${cookieValue || ''}${expiryDate}; path=/`;
  }

  getCookie(cookieKey){
    let cookieName = `${cookieKey}=`;

    let cookieArray = document.cookie.split(';');

    for (let cookie of cookieArray) {

      while (cookie.charAt(0) == ' ') {
            cookie = cookie.substring(1, cookie.length);
        }

      if (cookie.indexOf(cookieName) == 0) {
            return cookie.substring(cookieName.length, cookie.length);
        }
    }
  }

  getItem(array){
    let curCookie = this.getCookie('item-current');
    return array.find( function(item)
    {
      let prod = item['product'].toLowerCase();
      let itemCookie = unescape( curCookie ).toLowerCase();
      if( prod === itemCookie ) {
        return item;
      }
    });
  }

  showSingleItem(prod, container){
    let itemHtml = [ this.getItem(prod) ].map(({ link, image, product, price }, index) => {
      let [, integer, decimal] = price.match(/^(\d+\.?\-?)(\d*)/);
      let wid = link.split('?wid=')[1];
      return `<div class="col-sm-6 col-md-4 col-lg-3 text-center">
        <img id="item-img" class="img-thumbnail" src="${image}" alt="${product}"/>
      </div>
      <div class="col-sm-6 col-md-8 col-lg-7">
        <p id="item-wid">Wid: <small>${wid}</small></p>
        <p id="item-product">${product}</p>
        <p id="item-price"><span class="price">${integer}${decimal ? "" : "-"}<span class="cents">${
            decimal ? (decimal + "0").slice(0, 2) : ""
          }</span></span></p>
        <p><a href="#" class="btn btn-success btn-sm" title="basket">buy item</a></p>
      </div>`;
    }).join("");

    document.getElementById(container).innerHTML = itemHtml;
  }
}

window.onload = function(){

  (async function() {
  	const products = await fetch("json/data.json").then(response =>
  		response.json()
  	);

  	//const itemProd = getItem(products);
    //console.log( [itemProd] );
    const singlPage = new SingleItem();
    singlPage.showSingleItem(products, "item_page");

  })();

};